.App {
  text-align: center;
}

.graph{height: 600px;}
.graph svg text{
  fill: navy;
}

.header{
  width: 100%;
  text-align: center;
  background: rgb(161, 121, 160);
}
.nav {
  font-size: 30px;
  text-decoration: none;
  color: rgb(51, 48, 48);
  display: inline-block;
  margin: 10px 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
