body{
    background: rgb(107, 101, 101);

}

.today{
    margin: 0; padding: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.today li{
    width: 100%;
    list-style: none;
    margin: 0; padding: 10px 0;
    width: calc(100%/7);
    text-align: center;
}
.today li p{
    margin: 0; padding: 0;
}

.today li p:first-child {
    font-size: 20px;
    color: white;
}
.today li p:last-child {
    font-size: 40px;
    color: yellow;
}
.data{
    padding: 50px 0;
}

.data .select{
    width: 15%;
    padding: 20px;
    border: none;
    outline: none;
    display: block;
    margin: 0 auto;
    border-radius: 30px;
    background: rgb(226, 203, 225);
    text-align: center;
    font-size: 30px;
}


