h2{
    text-align: center;
}

.box{
    display: flex; justify-content: center; align-items: center;
    flex-wrap: wrap; flex-shrink: 0;
}

.city{
    width: 10%;
    border: 1px solid black;
    margin: 10px 20px;
    text-align: center;
}
.city p{
    margin: 0;
    padding: 15px 0;

}
.city p:nth-of-type(2){
    font-size: 30px;
    color: white;
    font-weight: bold;
}

.box_bar{
    margin: 0; padding: 0;
    height: 1000px;
    width: 50%;
    display: inline-block;
}
.box_pie{
    margin: 0; padding: 0;
    display: inline-block;
    height: 1000px;
    width: 50%;
}